import api from '../api/api';
import {exchangeString01 } from './api';

export async function login01(pwd) {
    try {
        pwd = exchangeString01(pwd);
        const response = await fetch(`${api}/api/blog/login01`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({pwd}),
        });
        if (response.ok) {
          //  
          const data = await response.json(); // JSON  
          return data; //  
        } else {
          //  
          console.error('login01 ');
        }
      } catch (error) {
        console.error('login01 ', error);
      }
}