
import api from './api';

export async function Search01(board_type) {
    try {
        const response = await fetch(`${api}/api/blog/BoardListSearch`, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({board_type}),
        });
        if (response.ok) {
          //  
          const data = await response.json(); // JSON  
          return data; //  
        } else {
          //  
          console.error('Search01 ');
        }
      } catch (error) {
        console.error('Search01 ', error);
      }
}